@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap");

.card-three-main {
  width: 800px;
  min-height: 475px;
  background-image: url("./pic1.jpg");
  background-size: 100% 100%;
  background-position: center;
  position: relative;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-three-first-div-top {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(248, 248, 248, 0.596);
  opacity: 0.6;
  z-index: 2;
  border-radius: 10px;
}

.card-three-first-div {
  width: 70%;
  min-height: 80%;
  background-color: #fff;
  position: absolute;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px 30px;
  z-index: 3;
}

.card-three-first-div h1 {
  text-align: center;
  margin-bottom: 5px;
}

.card-three-first-div h3 {
  text-align: center;
  line-height: 1.5;
  font-weight: 400;
}

.card-three-first-div button {
  color: #fff;
  background-color: #612d91;
  width: 120px;
  height: 40px;
  border: none;
  margin-top: 10px;
  font-size: medium;
}

.card-three-main a{

  color: #fff;
  text-decoration: none;
}

@media only screen and (max-width: 768px) {
  .card-three-main {
    width: 600px;
    height: 400px;
  }

  .card-three-first-div h1 {
    font-size: x-large;
    margin-bottom: 5px;
  }

  .card-three-first-div h3 {
    font-size: large;
    font-weight: 400;
  }
}

@media only screen and (max-width: 767px) and (min-width: 425px) {
  .card-three-main {
    width: 350px;
    height: 300px;
  }

  .card-three-first-div {
    width: 92.5%;
    height: 90%;
  }
  .card-three-first-div h1 {
    font-size: large;
    margin-bottom: 5px;
  }

  .card-three-first-div h3 {
    font-size: medium;
    font-weight: 400;
    line-height: 1.2;
  }
}

@media only screen and (max-width: 425px) and (min-width: 375px) {
  .card-three-main {
    width: 350px;
    height: 300px;
  }

  .card-three-first-div {
    width: 92.5%;
    height: 90%;
    padding: 5px 30px;
    justify-content: space-evenly;
  }

  .card-three-first-div h1 {
    font-size: large;
    margin-bottom: 5px;
  }

  .card-three-first-div h3 {
    font-size: medium;
    font-weight: 400;
    line-height: 1.25;
  }
}

@media only screen and (max-width: 374px) {
  .card-three-main {
    width: 275px;
    min-height: fit-content;
  }

  .card-three-first-div {
    width: 85%;
    min-height: 90%;
    justify-content: space-evenly;
  }
  .card-three-first-div h1 {
    font-size: large;
    margin-bottom: 5px;
  }

  .card-three-first-div h3 {
    font-size: medium;
    font-weight: 400;
    line-height: 1.4;
  }
}

@media only screen and (max-width: 320px) {
  .card-three-main {
    min-height: 475px;
  }

  .card-three-first-div h3 {
    font-size: smaller;
  }

  .card-three-first-div {
    padding: 10px 10px;
  }
}
