@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap");

.cities-video-background {
  position: relative;
  width: 100%;
  height: calc(100vh - 80px); /* Adjust the height as needed */
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 5vw;
}

#cities-my-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.cities-content {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  gap: 2.5vh;
  /* Add styles for your content */
}

.cities-content h1 {
  color: #fff;
  /* font-family: "Comfortaa", cursive; */
  font-size: 4rem;
  text-align: center;
}

.cities-content h2 {
  color: #fff;
}

.cities-content button {
  background-color: #612d91;
  color: #fff;
  border: none;
  width: fit-content;
  padding: 2vh 2vw;
  font-size: small;
}

@media only screen and (max-width: 425px) {

  .cities-content h1 {
    font-size: 3rem;
  }
}
