@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap");

.card-two-main {
  position: relative;
}

.bottom-div {
  width: 60vw;
  height: 80vh;
  border: 5px solid #fff;
}

.top-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  width: 60vw;
  height: 80vh;
  top: -40px;
  left: 40px;
  background-color: green;
  padding: 5vh;
  background-size: 100% 100%;
}

.top-div-partition-one {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.5vh;
  padding-top: 2vh;
  padding-bottom: 2vh;
}

.top-div-partition-one h1 {
  /* font-family: "Comfortaa", cursive; */
  font-size: 2.5rem;
  font-weight: bold;
  color: #fff;
}

.top-div-partition-one h2 {
  /* font-family: "Comfortaa", cursive; */
  font-size: 1.5rem;
  font-weight: light;
  line-height: 1.5;
  color: #fff;
}

/* .top-div-partition-two {
  width: 50%;
} */

@media only screen and (max-width: 768px) {

  .top-div-partition-one h1 {
    font-size: 2rem;
  }

  .top-div-partition-one h2 {
    font-size: 1.25rem;
  }
}

@media only screen and (max-width: 767px) and (min-width: 425px) {

  .top-div-partition-one h1 {
    font-size: 1.4rem;
  }

  .top-div-partition-one h2 {
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 424px) and (min-width: 375px) {

  .top-div-partition-one h1 {
    font-size: 1.25rem;
  }

  .top-div-partition-one h2 {
    font-size: 0.775rem;
  }
}

@media only screen and (max-width: 374px) {

  .top-div-partition-one h1 {
    font-size: 1.2rem;
  }

  .top-div-partition-one h2 {
    font-size: 0.65rem;
  }
}